.menu-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu-item {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  padding: 0 10px;
}
.menu-item:hover a::before,
.menu-item:hover .menu-link::before {
  opacity: 1;
  transform: none;
}
.menu-item + .menu-item {
  margin-left: 35px;
}
.menu-item.current_page_item {
  font-family: "Proxima Nova", sans-serif;
  font-weight: 600;
}
.menu-item a {
  position: relative;
}
.menu-item a::before {
  content: "";
  position: absolute;
  bottom: -3px;
  height: 1px;
  background-color: #494949;
  width: 100%;
  transform: translateY(7px);
  opacity: 0;
  transition: all 0.3s ease;
}

.hamburger {
  position: relative;
  z-index: 12;
  vertical-align: middle;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  transition: 0.2s ease-out;
}
.hamburger, .hamburger[type=button] {
  cursor: pointer;
  border: 0;
  padding: 9px 5px;
  background-color: transparent;
}
.hamburger__box {
  display: block;
  width: 30px;
  padding: 9px 0;
}
.hamburger__inner {
  position: relative;
  transition: background-color 0s ease-out 0.1s;
}
.hamburger__inner, .hamburger__inner::before, .hamburger__inner::after {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #494949;
}
.hamburger__inner::before, .hamburger__inner::after {
  content: "";
  position: absolute;
  left: 0;
  transition: bottom 0.1s ease-out 0.1s, top 0.1s ease-out 0.1s, transform 0.1s ease-out;
}
.hamburger__inner::before {
  bottom: 8px;
}
.hamburger__inner::after {
  top: 8px;
}

.is-active .hamburger__inner {
  background-color: transparent;
}
.is-active .hamburger__inner::before, .is-active .hamburger__inner::after {
  transition: bottom 0.1s ease-out, top 0.1s ease-out, transform 0.1s ease-out 0.1s;
}
.is-active .hamburger__inner::before {
  bottom: 0;
  transform: rotate(45deg);
}
.is-active .hamburger__inner::after {
  top: 0;
  transform: rotate(-45deg);
}

@media (max-width: 1100px) {
  .menu .menu-item + .menu-item {
    margin-left: 20px;
  }
}
@media (max-width: 992px) {
  .menu .menu-item {
    display: block;
  }
  .menu .menu-item + .menu-item {
    margin-left: 0;
    margin-top: 5px;
  }
}